class Button extends Hover(Base(PolymerElement)) {
  static get is() {
    return `${elementPrefix}-button`;
  }

  static get properties() {
    return {
      type: {
        type: String,
        value: 'default',
        reflectToAttribute: true,
      },
      size: {
        type: String,
        value: 'default',
        reflectToAttribute: true,
      },
      disabled: {
        type: Boolean,
        reflectToAttribute: true,
      },
      plain: {
        type: Boolean,
        reflectToAttribute: true,
      },
      loading: {
        type: Boolean,
        reflectToAttribute: true,
      },
      formType: {
        type: String,
      },
      openType: {
        type: String,
        value: '',
      },
      hoverStartTime: {
        type: Number,
        value: 20,
      },
      hoverStayTime: {
        type: Number,
        value: 70,
      },
      hoverClass: {
        type: String,
        value: 'button-hover',
        observer: '_hoverClassChange',
      },
    };
  }

  static get template() {
    return html`
      <style include="button-style">
      </style>
      <i id="icon" if="[[loading]]"></i>
      <slot></slot>
      <template is="dom-if" if="[[showMask(disabled, type)]]">
        <div id="mask"></div>
      </template>
    `;
  }

  get listeners() {
    return {
      tap: '_onButtonTap',
    };
  }

  connectedCallback() {
    super.connectedCallback();

    this._defaultHoverClass = 'button-hover';
  }

  _onButtonTap() {
    const _this2 = this;

    if (this.disabled) {
      return;
    }

    if (this.formType) {
      if (this.formType === 'submit') {
        this.dispatchEvent(new CustomEvent('formSubmit', {
          bubbles: true,
          composed: true,
        }));
      }

      if (this.formType === 'reset') {
        this.dispatchEvent(new CustomEvent('formReset', {
          bubbles: true,
          composed: true,
        }));
      }
    }

    if (!this._lock && this.openType) {
      this._lock = true;
      setTimeout(() => {
        _this2._lock = false;
      }, 1000);

      if (this.openType === 'share') {

      } else if (this.openType === 'getPhoneNumber') {

      } else if (this.openType === 'launchApp') {

      } else if (this.openType === 'contact') {

      }
    }
  }

  showMask(disabled, type) {
    return disabled && type === 'default';
  }
}

window.customElements.define(Button.is, Button);
